@import "../global/variables";

.post {
  position: relative;
  display: flex;
  flex-direction: column;
  padding: 24px 20px 20px;
  font-size: 14px;
  line-height: 22px;

  @media (min-width: $tablet) {
    width: 50%;
    padding: 0;

    &:first-child {
      width: 100%;

      .post__image {
        height: 400px;
      }
    }

    &:nth-child(2n + 3) {
      .post__wrapper {
        background-color: #eaeaea;
      }
    }
  }

  @media (min-width: $desktop) {
    width: 33.3%;

    &:nth-child(3n - 1) {
      width: 33.4%;
    }

    &:nth-child(2n + 3) {
      .post__wrapper {
        background: rgba(0, 0, 0, 0.3);
      }
    }
  }
}

.post__wrapper {
  @media (min-width: $tablet) {
    display: flex;
    align-items: flex-start;
    height: 100%;
    padding: 20px 20px 16px;
    background-color: #f2f2f2;
  }

  @media (min-width: $desktop) {
    box-sizing: border-box;
    position: absolute;
    bottom: 0;
    left: 0;
    margin-top: auto;
    width: 100%;
    height: auto;
    color: #ffffff;
    background: rgba(0, 0, 0, 0.3);
  }
}

.post__text-wrapper {
  flex-grow: 1;
}

.post__title {
  margin: 0;
  font-size: 14px;
  line-height: 21px;

  @media (min-width: $tablet) {
    line-height: 22px;
  }
}

.post__image {
  display: block;
  width: 100%;
  margin-bottom: 15px;
  object-fit: cover;
  color: #ffffff;
  background-color: #491213;

  @media (min-width: $tablet) {
    height: 267px;
    margin-bottom: 0;
  }
}

.post__author {
  margin: 0;
  line-height: 20px;
  text-transform: none;

  @media (min-width: $tablet) {
    line-height: 22px;
  }
}

.post__like {
  display: flex;
  gap: 2px;
  align-items: center;
  margin-left: -10px;

  @media (min-width: $tablet) {
    margin-left: auto;
  }
}

.post__like-button {
  width: 44px;
  height: 44px;
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
  color: rgba(0, 0, 0, 0.3);

  &:hover,
  &:focus {
    color: #81b3d2;
  }

  &:active {
    opacity: 0.6;
  }

  @media (min-width: $tablet) {
    width: 40px;
    height: 40px;
  }

  @media (min-width: $desktop) {
    color: #ffffff;
  }
}

.post__like-counter {
  padding: 6px 0 0;
  font-weight: 700;
}
