@font-face {
  font-family: "PT Sans";
  src: local("PT Sans"), url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  src: local("PT Sans Bold"), url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
.visually-hidden {
  height: 1px;
  overflow: hidden;
  width: 1px;
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(50%);
  white-space: nowrap;
}

.page {
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 14px;
  line-height: 21px;
  text-transform: uppercase;
}

.page__body {
  margin: 0;
  padding: 0;
  background-color: #f2f2f2;
}

.page__wrapper {
  max-width: 1200px;
  margin: 0 auto;
  background-color: #ffffff;
  box-shadow: 0 5px 15px rgba(0, 1, 1, 0.2);
}

.header {
  min-height: 56px;
  background-color: #ffffff;
}

.header__logo {
  z-index: 1;
  position: absolute;
  top: 0;
  left: calc(50vw - 92px);
}

.navigation {
  z-index: 2;
  position: relative;
}
.navigation--no-js .navigation__toggle {
  display: none;
}
.navigation--no-js .navigation__list {
  position: unset;
  display: flex;
}
@media (min-width: 768px) {
  .navigation {
    z-index: 0;
  }
}

.navigation__list {
  box-sizing: border-box;
  position: absolute;
  display: none;
  flex-direction: column;
  align-items: stretch;
  gap: 1px;
  margin: 0;
  padding: 0;
  top: 0;
  left: 0;
  width: 100vw;
  list-style: none;
  background-color: #81b3d2;
}
.navigation__list--opened {
  display: flex;
}
.navigation__list::after {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100vw;
  height: 8px;
  content: "";
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
}
@media (min-width: 768px) {
  .navigation__list {
    max-width: 100%;
    position: unset;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    background-color: transparent;
  }
  .navigation__list::after {
    content: none;
  }
}

.navigation__item {
  box-shadow: 0 1px 0 rgba(1, 1, 1, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.2);
}
.navigation__item:first-child {
  box-shadow: 0 1px 0 rgba(1, 1, 1, 0.2);
}
.navigation__item:last-child {
  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.2);
}
.navigation__item > .navigation__link {
  color: #ffffff;
}
@media (min-width: 768px) {
  .navigation__item {
    min-width: calc(20% - 1px);
    box-shadow: none;
  }
  .navigation__item:nth-child(2) {
    margin-right: 20%;
  }
  .navigation__item:nth-child(6) {
    margin-right: 20%;
  }
  .navigation__item:first-child, .navigation__item:last-child {
    box-shadow: none;
  }
  .navigation__item > .navigation__link {
    color: #000000;
  }
  .navigation__item--current > .navigation__link {
    color: #766357;
  }
}

.navigation__link {
  display: block;
  padding: 16px 56px 13px;
  text-align: center;
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  text-transform: uppercase;
  text-decoration: none;
}
.navigation__link:focus, .navigation__link:hover {
  background-color: #669ec0;
}
.navigation__link:active {
  background-color: #5496bd;
  color: rgba(255, 255, 255, 0.3);
}
.navigation__link--current:focus, .navigation__link--current:hover, .navigation__link--current:active {
  color: #ffffff;
  background-color: #81b3d2;
}
@media (min-width: 768px) {
  .navigation__link {
    padding: 17px;
    font-size: 14px;
    line-height: 22px;
    background-color: transparent;
  }
  .navigation__link:focus, .navigation__link:hover, .navigation__link:active {
    background-color: transparent;
  }
  .navigation__link:focus, .navigation__link:hover {
    color: #81b3d2;
  }
  .navigation__link:active {
    color: rgba(0, 0, 0, 0.3);
  }
  .navigation__link--current:focus, .navigation__link--current:hover, .navigation__link--current:active {
    color: #766357;
  }
}

.navigation__toggle {
  z-index: 1;
  top: 0;
  right: 0;
  width: 56px;
  height: 56px;
  position: absolute;
  font-size: 0;
  padding: 0;
  border: none;
  background-color: transparent;
  background-position: center;
  background-repeat: no-repeat;
  background-image: url("../img/icon-menu.svg");
  cursor: pointer;
}
.navigation__toggle--opened {
  background-image: url("../img/icon-cross.svg");
}
.navigation__toggle:focus, .navigation__toggle:hover {
  opacity: 0.6;
}
.navigation__toggle:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .navigation__toggle {
    display: none;
  }
}

.hero {
  position: relative;
  padding: 90px 20px 85px;
  color: #ffffff;
  background-color: #7c97a3;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-image: url("../img/back-mobile.jpg");
}
@media (min-resolution: 2dppx) {
  .hero {
    background-image: url("../img/back-mobile@2x.jpg");
  }
}
.hero--inner {
  background-position: bottom;
  margin: 0;
  padding: 52px 20px 68px;
}
.hero--inner .hero__image {
  height: 40px;
}
.hero::after {
  position: absolute;
  bottom: 0;
  left: 0;
  min-width: 100%;
  height: 57px;
  content: "";
  background-repeat: no-repeat;
  background-position: center;
  background-image: url("../img/intro-triangle.svg");
}
@media (min-width: 768px) {
  .hero {
    background-image: url("../img/back-desktop.jpg");
    background-size: 1200px auto;
    padding: 78px 20px 90px;
  }
  .hero--inner {
    padding: 95px 60px 118px;
  }
  .hero--inner .hero__image {
    height: 78px;
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .hero {
    background-image: url("../img/back-desktop@2x.jpg");
  }
}
@media (min-width: 1200px) {
  .hero--inner {
    padding-top: 100px;
    padding-bottom: 122px;
  }
}

.hero__image {
  display: block;
  height: 214px;
  width: auto;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .hero__image {
    height: 350px;
  }
}

.features {
  background-color: #ffffff;
  overflow-x: hidden;
}

.features__header {
  position: relative;
  margin: 0 auto;
  padding: 32px 20px;
  background-size: cover;
  background-position: center;
}
.features__header::after {
  z-index: 1;
  position: absolute;
  bottom: -21px;
  left: 0;
  min-width: 100%;
  max-width: 768px;
  height: 37px;
  content: "";
  background-repeat: no-repeat;
  background-position: center;
  background-image: url("/img/triangle.svg");
}
@media (min-width: 768px) {
  .features__header {
    padding: 48px 120px 56px;
  }
  .features__header::after {
    content: none;
  }
}
@media (min-width: 1200px) {
  .features__header {
    padding: 60px 120px 46px;
  }
}

.features__title {
  margin: 0 0 38px;
  text-align: center;
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
}
@media (min-width: 768px) {
  .features__title {
    margin-bottom: 28px;
    font-size: 21px;
  }
}

.features__subtitle {
  margin: 0;
  text-align: center;
}

.features__list {
  display: grid;
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (min-width: 768px) {
  .features__list {
    grid-template-columns: 1fr 1fr 1fr;
    grid-auto-flow: row;
  }
}

.feature {
  position: relative;
  box-sizing: border-box;
  padding: 40px 20px 0;
}
.feature:first-child {
  padding-top: 54px;
}
.feature--accent {
  background-color: #81b3d2;
  color: #ffffff;
}
.feature--accent .feature__text {
  margin-bottom: 40px;
}
.feature--flipped .feature__text {
  margin-bottom: 34px;
}
@media (min-width: 768px) {
  .feature {
    padding: 0;
  }
  .feature:first-child {
    padding-top: 0;
  }
  .feature--accent {
    grid-column: 1/4;
    display: flex;
  }
  .feature--accent .feature__text-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    box-sizing: border-box;
    width: 33.3%;
    padding: 40px 30px 44px;
  }
  .feature--accent .feature__text {
    margin-bottom: 0;
  }
  .feature--flipped {
    flex-direction: row-reverse;
  }
}
@media (min-width: 1200px) {
  .feature--accent .feature__text-wrapper {
    padding: 50px;
  }
}

.feature__title {
  margin: 0 0 10px;
  font-size: 18px;
  line-height: 26px;
  text-align: center;
}
.feature__title br {
  display: none;
}
@media (min-width: 768px) {
  .feature__title {
    margin-left: auto;
    margin-right: auto;
    font-size: 21px;
    margin-bottom: 12px;
  }
  .feature__title br {
    display: block;
  }
}

.feature__counter {
  margin: 0 0 18px;
  text-align: center;
}
@media (min-width: 768px) {
  .feature__counter {
    margin-bottom: 24px;
  }
}

.feature__text {
  margin: 0;
  text-align: center;
}

.feature__image {
  display: block;
  width: 100vw;
  object-fit: cover;
  margin: 0 -20px;
  padding: 0;
  background-color: #491213;
}
@media (min-width: 768px) {
  .feature__image {
    width: 66.7vw;
    max-height: 256px;
    max-width: 800px;
    margin: 0;
  }
}

.feature__icon {
  padding-top: 30px;
  padding-left: 68px;
}
.feature__icon .feature__title {
  margin-bottom: 16px;
  text-align: left;
}
.feature__icon .feature__text {
  text-align: left;
}
.feature__icon::before {
  position: absolute;
  top: 24px;
  left: 18px;
  width: 40px;
  height: 40px;
  content: "";
  background-repeat: no-repeat;
  background-size: contain;
}
.feature__icon--house::before {
  background-image: url("../img/house.svg");
}
.feature__icon--burger::before {
  background-image: url("../img/burger.svg");
}
.feature__icon--gift {
  padding-bottom: 52px;
}
.feature__icon--gift::before {
  background-image: url("../img/present.svg");
}
@media (min-width: 768px) {
  .feature__icon {
    padding: 0;
  }
  .feature__icon .feature__title {
    text-align: center;
    margin-bottom: 24px;
  }
  .feature__icon .feature__text {
    text-align: center;
  }
  .feature__icon .feature__text-wrapper {
    box-sizing: border-box;
    padding: 160px 32px 60px;
  }
  .feature__icon::before {
    top: 60px;
    left: calc(50% - 40px);
    height: 75px;
    width: 75px;
    background-size: 75px;
  }
}
@media (min-width: 1200px) {
  .feature__icon .feature__text-wrapper {
    padding: 160px 50px 78px;
  }
}

.feature--divided {
  position: relative;
  padding: 25px 20px;
  background-color: #eeeeee;
}
.feature--divided::after {
  position: absolute;
  bottom: 0;
  left: 20px;
  width: calc(100% - 40px);
  height: 1px;
  content: "";
  background-color: #d6d6d6;
}
.feature--divided:last-child::after {
  content: none;
}
@media (min-width: 768px) {
  .feature--divided {
    padding: 0;
  }
  .feature--divided .feature__text-wrapper {
    padding: 40px;
  }
  .feature--divided::after {
    content: none;
  }
}
@media (min-width: 1200px) {
  .feature--divided .feature__text-wrapper {
    padding: 44px 64px 60px;
  }
}

